@charset "UTF-8";

main {
  min-width: 1050px;
  height: calc(100vh - 55px);
  margin: 0px auto;
  margin-top: 55px !important;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1000px) {
  main {
    width: 100%;
    height: 100%;
  }
}
