@charset "UTF-8";

// 변수 설정
:root {
  --white: #fff;
  --black: #000;
}

// border-box 초기화
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

// 가로모드 글씨 확대 초기화
html {
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

// 공백 초기화
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
select,
table,
td,
textarea,
th,
ul,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

// 폰트 초기화
body,
button,
input,
select,
table,
textarea {
  font-size: 16px;
  line-height: 1.4;
  letter-spacing: -0.2px;
  color: #202020;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  width: 100%;
  height: 100%;
}

// 링크 초기화
a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
}

// 버튼 초기화
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

// 스타일 초기화
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

ul {
  list-style: none;
}

em,
address {
  font-style: normal;
}

strong {
  font-weight: normal;
}

img {
  vertical-align: top;
  width: 100%;
  display: inline-block;
  content: "";
  border-style: hidden;
  border: 0px;
  border-width: 0px;
}
// table
table {
  border: 0;
  border-collapse: collaspe;
  border-spacing: 0;
  width: 100%;
}

// 간격 설정
.mt0 {
  margin-top: 0 !important;
}

// 디자인 초기화
input,
textarea,
button,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -o-border-radius: 0;
  border-radius: 0;
  font-size: 16px;
  background: transparent;
  border: none;
}

// 인풋 포커스
input:focus {
  outline: none !important;
  background-color: #fff;
  border: transparent;
  box-shadow: 0 0 3px #eee;
}
